import { observer } from 'mobx-react-lite';
import s from './events-table.module.scss';
import { blocksStore } from 'store/candy/explorer/blocks.store';
import { useHistory } from 'react-router-dom';

export const Table = observer(() => {
  const history = useHistory();
  
  const goToBlock = (blockIndex: number) => {
    if(blockIndex == 0){
      return;
    }

    history.push(`/explorer/block/${blockIndex}`);
  }

  return (
    <div className={s.events}>
      {
        blocksStore.events.map(({ block, contract, type, hash, from, to, date }) =>
          <div key={block} className={`${s.event}`}>
            <div className={s.header}>
              <div className={s.block}>block: #<a onClick={() => goToBlock(block)}>{block}</a></div>
              <div className={s.type}>type: {type}</div>
              <div className={s.date}>date: {date}</div>
              <div className={s.contract}>contract: {contract}</div>
            </div>
            <div className={s.body}>
              <div className={s.hash}>hash: {hash}</div>
              <div className={s.from}>from: {from}</div>
              <div className={s.to}>to: {to}</div>
            </div>
          </div>
        )
      }
    </div>
  );

});
